@import "../../libs/include-media/dist/include-media";
@import "../utils/special-version-variables";
@import "../utils/mixins";

.#{$spec-mod} {

	.promo-slider,
	.info__list,
	.spec-slider-reset {
		&, &.slick-dotted {
			font-size: inherit;
			line-height: inherit;
			letter-spacing: inherit; }

		&.slick-slider {
			user-select: auto !important; }

		.slick-track {
			opacity: 1 !important;
			left: auto !important;
			width: auto !important;
			-webkit-transform: none !important;
			-ms-transform: none !important;
			transform: none !important;

			&::before, &::after {
				content: none; } }

		.slick-slide {
			position: relative !important;
			width: auto !important;
			height: auto !important;
			opacity: 1 !important;
			left: auto !important;
			top: auto !important;
			float: none !important;
			z-index: auto !important; }

		.slick-arrow {
			display: none !important; }

		.slick-dots {
			display: none !important; } }

	// tape slider
	.tape-slider__arr, .tape-slider__scrollbar {
		display: none !important; }

	.tape-slider__frame {
		overflow: visible !important; }

	.tape-slider__holder {
		transform: none !important;
		width: auto !important;
		height: auto;
		@include spec-font-reset;

		&::before {
			display: none; } }

	.products__item {
		width: auto !important;
		border: none;
		margin-bottom: $spec-bottom-space-item; } }
