@import "../libs/include-media/dist/include-media";
@import "utils/special-version-variables";
@import "utils/functions";
@import "utils/mixins";
@import "utils/placeholders";

/*=========== SPECIAL VERSION =========== */
.vspec--hide-page {
	display: none !important; }

body.#{$spec-mod} {
	//+spec-font-size--normal
	font-family: $spec-font-style--sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: $spec-font-size--medium;
	line-height: $spec-line-height--small;
	color: $spec-text-color;
	background: $spec-bg-color url(../img/empty.png) no-repeat fixed; }

/* special version settings */
/* ============================================================ */

body.#{$spec-mod} {
	// >>> Font Style <<<
	// Специальная версия: Шрифт без засечек (sans-serif)
	// ================================================================
	.#{$spec-mod--sans-serif} & {
		font-family: $spec-font-style--sans-serif; }

	// Специальная версия: Шрифт с засечками (serif)
	// ================================================================
	.#{$spec-mod--serif} & {
		font-family: $spec-font-style--serif; }

	// Специальная версия: Моноширинный шрифт (monospace)
	// ================================================================
	.#{$spec-mod--monospace} & {
		font-family: $spec-font-style--monospace; }

	// >>> Font Size <<<
	// Специальная версия: Стандартный базовый шрифт
	// ================================================================
	.#{$spec-mod--font-normal} & {
		font-size: $spec-font-size--medium; }

	// Специальная версия: Уменьшенный базовый шрифт
	// ================================================================
	.#{$spec-mod--font-small} & {
		font-size: $spec-font-size--small; }

	// Специальная версия: Увеличенный базовый шрифт
	// ================================================================
	.#{$spec-mod--font-large} & {
		font-size: $spec-font-size--large; }

	// >>> Line Height <<<
	// Специальная версия: Междустрочный интервал - Одинарный
	// ================================================================
	.#{$spec-mod--line-height-small} & {
		line-height: $spec-line-height--small; }

	// Специальная версия: Междустрочный интервал - Полуторный (нормальный)
	// ================================================================
	.#{$spec-mod--line-height-medium} & {
		line-height: $spec-line-height--medium; }

	// Специальная версия: Междустрочный интервал - Двойной
	// ================================================================
	.#{$spec-mod--line-height-large} & {
		line-height: $spec-line-height--large; }

	// >>> Letter Spacing <<<
	// Специальная версия: Средний межбуквенный интервал
	// ================================================================
	.#{$spec-mod--spacing-medium} & {
		letter-spacing: $spec-letter-spacing--medium;
		word-spacing: $spec-letter-spacing--medium; }

	// Специальная версия: Бальшой межбуквенный интервал
	// ================================================================
	.#{$spec-mod--spacing-large} & {
		letter-spacing: $spec-letter-spacing--large;
		word-spacing: $spec-letter-spacing--large; }

	// >>> Color scheme <<<
	// Специальная версия: Цветовая схема - Белым по черному
	// ================================================================
	.#{$spec-mod--scheme-wb} & {
		color: $spec-text-color--scheme-wb;
		background-color: $spec-bg-color--scheme-wb; }

	// Специальная версия: Цветовая схема - Синим по голубому
	// ================================================================
	.#{$spec-mod--scheme-blue} & {
		color: $spec-text-color--scheme-blue;
		background-color: $spec-bg-color--scheme-blue; }

	// Специальная версия: Цветовая схема - Коричневым по бежевому
	// ================================================================
	.#{$spec-mod--scheme-brown} & {
		color: $spec-text-color--scheme-brown;
		background-color: $spec-bg-color--scheme-brown; }

	// Специальная версия: Цветовая схема - Зеленым по темно-коричневому
	// ================================================================
	.#{$spec-mod--scheme-green} & {
		color: $spec-text-color--scheme-green;
		background-color: $spec-bg-color--scheme-green; } }

/* special version reset */
/* ============================================================ */

// reset all styles
// ------------------------------------------------------------
@import "spec/reset-full";

// reset font
// ------------------------------------------------------------
@import "spec/fonts-reset";

// reset transition
// ------------------------------------------------------------
@import "spec/transition";

// hide elements
// ------------------------------------------------------------
@import "spec/hide";

// reset svg icon color
// ------------------------------------------------------------
@import "spec/svg";

// special version background color
// ------------------------------------------------------------
@import "spec/bg";

// special reset padding
// ---------------------------------------------------------------------------
@import "spec/padding";

// special vertical space
// ---------------------------------------------------------------------------
@import "spec/spaces";

// reset images
// ------------------------------------------------------------
@import "spec/img";

// reset slider
// ------------------------------------------------------------
@import "spec/slider";

// layout spaces
// ------------------------------------------------------------
@import "spec/layout";

/* special version typography */
/* ============================================================ */

@import "spec/typography";

/* special version components */
/* ============================================================ */

/* to top */
.#{$spec-mod} {
	.btn-to-top {
		opacity: 0.5;

		i {
			&::before, &::after {
				background-color: currentColor; } }

		.no-touchevents &:hover {
			opacity: 0.9; } } }

/* to top end */

.#{$spec-mod} {
	&, &.home-page {
		.layout {
			max-width: calc(#{$spec-text-page} + #{nth($spec-page-space, 2)} * 2);
			margin: 0 auto;
			box-sizing: border-box;
			@include padding-horizontal(nth($spec-page-space, 2));

			//+media('<desk-hd')
			//ax-width: calc(#{$spec-text-page} + #{nth($spec-page-space, 2)} * 2 + #{nth($spec-panel-size, 2)})
			//include padding-horizontal(calc(#{nth($spec-page-space, 2)} + #{nth($spec-panel-size, 2)}), nth($spec-page-space, 2))

			@include media('<desk-sm') {
				max-width: none;
				//+padding-horizontal(calc(#{nth($spec-page-space--md, 2)} + #{nth($spec-panel-size, 2)}), nth($spec-page-space--md, 2))
				@include padding-horizontal(nth($spec-page-space--md, 2)); }

			@include media('<mob-md') {
				@include padding-horizontal(nth($spec-page-space--sm, 2)); } } }

	.header {
		color: currentColor; }

	.header-holder {
		min-height: 0;
		padding-top: 0;
		padding-bottom: calc(#{round($main-nav-height--spec / 2)} + 1em);

		@include media('<desk-ml') {
			padding-bottom: calc(#{round($main-nav-height--spec--sm / 2)} + 1em); }

		@include media('<tab-lg') {
			height: auto;
			padding-bottom: 0.5em;
			border: 0 solid currentColor;
			border-width: 0 0 1px; }

		@include media('<mob-lg') {
			display: flex;
			flex-direction: column;
			padding-bottom: 0;
			padding-top: 10px;
			border: none; }

		&::before, &::after {
			display: none; } }

	.main {
		padding-top: $spec-bottom-space-section;
		padding-bottom: $spec-bottom-space-section;

		@include media('<desk-ml') {
			$space: 2.5em;
			padding-top: $space;
			padding-bottom: $space; }

		@include media('<tab-sm') {
			$space: 2em;
			padding-top: $space;
			padding-bottom: $space; } }

	.content {
		//border: $spec-border-width solid $spec-text-color
		//margin-left: 0
		margin: 0 auto;
		max-width: $spec-text-layout;
		background: none;
		border: none;

		@include media('<desk-sm') {
			max-width: $spec-text-layout--sm; }

		@include media('<tab-sm') {
			max-width: none; } }

	&.home-page .content {
		max-width: none;

		.promo-section, .regard-section, .links-section, .info-center-section {
			max-width: $spec-text-layout; // костиль для главной страници, чтобы не ограничивать ширину блока с картой
			@include push-auto;

			@include media('<desk-sm') {
				max-width: $spec-text-layout--sm; }

			@include media('<tab-sm') {
				max-width: none; } } }

	// Убираем js пересчет размеров футера
	// Прижать к тизу нужно попытаться css методами
	// Пока не нашел подходящего решения.
	//&
	//isplay: flex
	//lex-direction: column
	//ustify-content: space-between
	//lign-items: stretch

	//wrapper
	//flex-shrink: 0 // нужно, если прижимать к низу с помощью flex
	//flex-grow: 0 // нужно, если прижимать к низу с помощью flex
	//overflow: visible // нужно, если прижимать к низу с помощью flex
	//min-height: 0
	//height: auto
	//width: 100% // нужно, если прижимать к низу с помощью flex

	.wrapper {
		min-height: 0;
		height: auto;
		overflow-y: visible;
		overflow-x: hidden;
		z-index: 6;

		//+media('<mob-md')
 }		//adding-top: nth($spec-panel-size--sm, 1)

	.spacer {
		display: none !important; }

	.footer {
		//width: 100% // нужно, если прижимать к низу с помощью flex
		margin-top: 0 !important;
		border-top: $spec-border-width solid currentColor; }

	.footer,
	.footer-gov,
	.footer-bottom {
		background: none; }

	.footer-common {
		padding-top: 1em; }

	.footer-bottom {
		padding-top: 1em;
		padding-bottom: 1em;

		@include media('<tab-lg') {
			padding-top: 0.5em; } }

	.footer-bottom-holder {
		padding-top: 0;
		padding-bottom: 0;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		line-height: 1;
		@include clear;

		@include media('<tab-lg') {
			display: block; }

		&::after {
			content: none; } }

	.footer-col {
		//float: left
		font-size: 18px;
		line-height: 24px;
		letter-spacing: $spec-letter-spacing--normal;
		padding: 0 10px;

		@include media('<tab-lg') {
			padding: 0.5em 0 0; }

		&:first-child {
			padding-left: 0; }

		&:last-child {
			padding-right: 0;
			text-align: right;

			@include media('<tab-lg') {
				text-align: left; } } }

	// developer
	.developer {
		.text-inner {
			display: inline; } }

	.developer_logo {
		display: inline;
		text-indent: 0;
		padding: 0;
		width: auto;
		height: auto;
		top: auto;

		&::before, &::after {
			display: none; } }

	// logo
	.logo {
		float: none;
		display: block;
		-webkit-tap-highlight-color: transparent;

		@include media('<tab-lg') {
			padding-left: 65px; } }

	.logo-img {
		@include media('<tab-lg') {
			left: 0; } }

	.logo-img, .logo::before {
		height: 100px;

		@include media('<1280px') {
			height: 80px; }

		@include media('<tab-lg') {
			height: 60px; } }

	.logo-title {
		text-align: left;

		& > strong {
			background-image: url(../img/logo-text--bw.svg);
			background-repeat: no-repeat;
			background-position: 0 50%;
			@include font(0, 0);
			//background-size: contain

			.#{$spec-mod--scheme-wb} & {
				background-image: url(../img/logo-text--wb.svg); }

			.#{$spec-mod--scheme-blue} & {
				background-image: url(../img/logo-text--blue.svg); }

			.#{$spec-mod--scheme-brown} & {
				background-image: url(../img/logo-text--brown.svg); }

			.#{$spec-mod--scheme-green} & {
				background-image: url(../img/logo-text--green.svg); } } }

	.copyright {
		vertical-align: top;
		padding: 0; }

	// main-nav
	.main-nav {
		height: $main-nav-height--spec;
		margin-top: -#{round($main-nav-height--spec / 2)};

		@include media('<desk-ml') {
			height: $main-nav-height--spec--sm;
			margin-top: -#{round($main-nav-height--spec--sm / 2)}; } }

	.main-nav-frame {
		box-shadow: none;
		transition: none;

		&::before {
			display: none; } }

	.nav-fixed & .main-nav-frame {
		&::before {
			display: none; } }

	.main-nav-holder {
		$shadow: 0 0 0 $spec-border-width;
		box-shadow: $shadow $spec-text-color;
		padding-left: $main-nav-height--spec;
		background: none;
		transition: none;

		.#{$spec-mod--scheme-wb} & {
			box-shadow: $shadow $spec-text-color--scheme-wb; }

		.#{$spec-mod--scheme-blue} & {
			box-shadow: $shadow $spec-text-color--scheme-blue; }

		.#{$spec-mod--scheme-brown} & {
			box-shadow: $shadow $spec-text-color--scheme-brown; }

		.#{$spec-mod--scheme-green} & {
			box-shadow: $shadow $spec-text-color--scheme-green; } }

	// navigation list
	.nav {
		box-shadow: none; }

	.nav__list {
		a {
			font-family: inherit; }

		& > li {
			& > a {
				display: flex;
				justify-content: center;
				align-items: center;
				height: $main-nav-height--spec;
				color: currentColor;
				@include spec-reset-bg;
				border: 0 solid currentColor;
				border-left-width: $spec-border-width;
				padding: 0 5px;

				@include media('<desk-ml') {
					height: $main-nav-height--spec--sm; }

				&:before {
					display: none; } }

			&:first-child > a {
				border-left-width: 0; }

			// hover
			&.hover > a,
			& > a:hover,
			& > a:focus {
				color: currentColor;
				text-decoration: none;
				border-color: currentColor; }

			&.has-drop.hover,
			&.current.hover_next,
			&.current.hover_prev {
				& > a {
					border-color: currentColor; } }

			// current
			&.current > a {
				text-decoration: none;
				@include spec-reset-bg-invert; } }

		[class*="nav-depict-icons-angle"] {
			&::before, &::after {
				background-color: currentColor; } } }

	[class*="nav__drop"] {
		border: $spec-border-width solid currentColor;
		box-shadow: none;
		//display: none

		a {
			padding: 10px 40px;
			@include font(18px, 24px);
			background: none;
			border-top: $spec-border-width solid currentColor;

			@include media('<desk-ml') {
				@include font(16px, 20px); } }

		li.hover > a {
			background: none; } }

	//.nav__list li.hover,
	//.no-js .nav__list li:hover,
	//.nav__list li.active
	// > [class*="nav__drop"]
	//display: block

	[class*="nav__list"] > li:first-child > [class*="nav__drop"] {
		border: $spec-border-width solid currentColor; }

	// button open menu / close popups

	.btn-menu, .btn-menu-close {
		@include size($main-nav-height--spec);
		display: block;
		display: flex;
		justify-content: center;
		line-height: 24px;
		align-items: center;
		color: $spec-text-color;
		transition: none;
		box-sizing: border-box;
		border: 0 solid currentColor;
		background-color: $spec-bg-color;
		text-decoration: underline;
		-webkit-tap-highlight-color: transparent;

		.#{$spec-mod--scheme-wb} & {
			background-color: $spec-bg-color--scheme-wb;
			color: $spec-text-color--scheme-wb; }

		.#{$spec-mod--scheme-blue} & {
			background-color: $spec-bg-color--scheme-blue;
			color: $spec-text-color--scheme-blue; }

		.#{$spec-mod--scheme-brown} & {
			background-color: $spec-bg-color--scheme-brown;
			color: $spec-text-color--scheme-brown; }

		.#{$spec-mod--scheme-green} & {
			background-color: $spec-bg-color--scheme-green;
			color: $spec-text-color--scheme-green; }

		&::before {
			display: none; }

		& > span {
			display: inline;
			vertical-align: middle;
			font-size: 18px;
			line-height: 24px;
			font-weight: bold;
			letter-spacing: $spec-letter-spacing--normal;
			text-transform: uppercase;

			@include media('<mob-md') {
				font-size: 14px; } }

		& > i {
			display: none;

			&, &::before, &::after {
				transition: none;
				background: currentColor; } } }

	.btn-menu {
		border-right-width: $spec-border-width;

		@include media('<desk-ml') {
			height: $main-nav-height--spec--sm; } }

	.btn-menu-mobile {
		display: none;
		border: none;
		height: auto;
		width: auto;
		background: none;
		position: absolute;
		right: 0;
		top: 0;
		margin-top: 0.4em;

		@include media('<tab-lg') {
			display: block; }

		@include media('<mob-lg') {
			margin-top: 24px; } }

	.user-options {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
		position: relative;
		top: auto;
		right: auto;
		width: auto;
		height: auto;
		padding: 0.5em 0;
		margin: 0;
		@include spec-font-size--note;
		background: none;

		@include media('<tab-lg') {
			border: 0 solid currentColor;
			border-width: 0 0 1px;
			margin-bottom: 0.5em;
			padding-right: 80px; }

		@include media('<mob-lg') {
			order: 3;
			padding-right: 0;
			margin-top: 10px;
			border-width: 1px 0; }

		li {
			position: relative; // В обычной версии на планшете два последних пункта позиционируются. Отменяем это
			right: auto; // В обычной версии на планшете два последних пункта позиционируются. Отменяем это
			top: auto; // В обычной версии на планшете два последних пункта позиционируются. Отменяем это
			left: auto; // В обычной версии на планшете два последних пункта позиционируются. Отменяем это
			bottom: auto; // В обычной версии на планшете два последних пункта позиционируются. Отменяем это
			height: auto;
			width: auto;
			padding: 0;
			margin: 0 1em 0 0;

			@include media('<mob-md') {
				display: block;
				margin: 0;
				flex-basis: 100%; } }

		a {
			padding: 0;
			height: auto;
			width: auto;
			color: currentColor;
			box-shadow: none;
			text-decoration: none;
			border: none;

			.touchevents {
				-webkit-tap-highlight-color: transparent; }

			&::before {
				display: none; }

			span {
				display: inline;
				vertical-align: middle;
				border: none;
				line-height: inherit;
				transition: none;
				text-decoration: underline;

				&.text-for-mobile {
					display: none; } }

			&:hover {
				background-color: transparent;
				color: currentColor;
				box-shadow: none;

				svg path {
					fill: currentColor; }

				span {
					text-decoration: none; } } }

		svg {
			//display: none
			display: inline-block;
			vertical-align: middle;
			@include size(24px);
			transition: none;

			path {
				fill: currentColor; } }

		.translate-ling {

			a {
				span {
					text-decoration: none; } } }

		.goog-te-gadget-simple {
			border-color: currentColor;
			@include padding-horizontal(0); } }

	// breadcrumbs
	.breadcrumbs-list {
		padding: 0;
		height: auto;
		min-height: 0;
		overflow: visible;
		@include spec-font-size--note;

		li, a, span {
			display: inline;
			vertical-align: baseline; }

		li {
			display: inline;
			padding: 0 0.4em 0 1em;

			&::before {
				color: inherit; }

			&, &:first-child {
				&::before {
					background: none;
					width: auto;
					height: auto;
					border: none;
					margin: 0; } }

			&:first-child {
				padding-left: 0; }

			span {
				line-height: inherit;
				border: none !important;
				white-space: normal; } }

		a {
			&:hover, &:focus {
				color: inherit; } } }

	// social networks
	.social-networks {
		li {
			padding-left: 0; }

		a {
			&::before {
				transition: none;
				border-color: currentColor; } }

		path {
			fill: currentColor; }

		// hover
		a:hover {
			path {
				fill: currentColor; } } }

	// content foot
	.content-foot {
		border: none;
		margin: 0;
		padding-top: $spec-bottom-space-section; }

	.user-content > .text-layout + .content-foot {
		margin-top: 0 !important; }

	.content-foot .share-box + div {
		border-top: $spec-border-width solid currentColor; }

	// share box
	.share-box__label {
		float: none; }

	.share-box {
		padding-bottom: $spec-bottom-space;

		.social-networks {
			float: none;
			margin: 0;
			padding: 0;
			text-align: left; } }

	// nav page
	.nav-page {
		font-size: inherit;
		letter-spacing: inherit;
		height: auto;
		padding: $spec-bottom-space 0 0;
		border-top: $spec-border-width solid currentColor; }

	.nav-page__item {
		display: block;
		border: none;
		float: none;
		text-align: left;
		color: inherit;

		a {
			display: inline;
			color: inherit;
			background: none;

			& > span {
				display: inline;
				vertical-align: initial;
				padding: 0;
				width: auto;
				font-size: inherit;
				line-height: inherit;
				font-family: inherit;
				text-transform: none;
				font-weight: bold;
				letter-spacing: inherit; }

			i {
				display: none;

				&::before, &::after {
					background-color: currentColor !important; } }

			&:hover {
				color: inherit;
				background-color: inherit; } }

		// disabled
		&.disabled {
			background: none;

			& a, & a:hover {
				text-decoration: none;
				cursor: default;
				color: inherit;
				opacity: 0.5;
				background-color: inherit; } } }

